body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
main {
  height: 100vh;
  width: 100%;
  font-family: sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
}
#panel {
  width: 33%;
  height: 100%;
  overflow-y: scroll;
  background-color: rgba(245, 245, 245, 1);
  /* color: white; */
  box-shadow: 10px 0px 10px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  text-align: center;
  -webkit-order: 0;
          order: 0;
  margin: 0;
  box-sizing: border-box;
  padding: 1rem;
  /* border-radius: 1rem; */
  /* z-index: 2; */
}
h1 {
  text-align: center;
  color: rgba(235, 175, 35, 1);
  text-transform: uppercase;
  /* word-wrap: break-word; */
}
h2 {
  text-align: center;
  /* color: white; */
  word-wrap: break-word;
}
h3 {
  margin: 0;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
li {
  border-bottom: solid rgba(235, 175, 35, 1) 1px;
  padding: 1em;
}
select {
  width: 80%;
  height: 48px;
}
#map-container {
  height: 100%;
  width: 66%;
  -webkit-order: 1;
          order: 1;
}
#map {
  height: 100%;
  width: 100%;
}
#info-window {
  text-align: center;
  margin: 0;
  /* max-height: 50%; */
  overflow-y: scroll;
}

#info-window img {
  height: auto;
  width: 100%;
}

.infoWindow {
  text-align: center;
  font-weight: bold;
  overflow-x: hidden;
  width: 100%;
  height:auto;
  margin: auto;
  border-top: solid rgba(235, 175, 35, 1) 1px;
  border-bottom: solid rgba(235, 175, 35, 1) 1px;
  /* transition: all 500ms ease-in-out; */
}

#panel button {
  min-height: 48px;
  min-width: 60%;
  border-radius: 48px;
  background-color: rgba(235, 175, 35, 1);
  padding: 0;
  border: 0;
  transition: 300ms all ease;
}

#panel button:hover, #panel button:focus {
  min-width: 80%;
  background-color: rgba(235, 175, 35, 0.9);
  box-shadow: 0 0 10px black;
}

@media screen and (max-width: 550px) {
  main {
    -webkit-flex-direction: column;
            flex-direction: column;

  }
  #map-container {
    width: 100%;
    height: 50%;
    -webkit-order: 0;
            order: 0;
  }
  #panel {
    /* border-radius: 1rem; */
    width: 100%;
    height: 50%;
    -webkit-order: 2;
            order: 2;
    box-shadow: 0px -10px 10px rgba(0, 0, 0, 0.5);
  }
}

